import {
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  Renderer2,
  SimpleChanges,
  NgZone,
} from '@angular/core';
import { fromEvent, Observable, Subject } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';

@Directive({
  selector: '[mdbTableEditor]',
  exportAs: 'mdbEditor',
})
export class MdbTableEditorDirective implements OnChanges {
  @Input() itemsPerPage = 10;
  @Output() rowHighlight: EventEmitter<any> = new EventEmitter<any>();

  private highlightedTableRow: any = null;
  private dataArrayUnmodified: any[] = [];

  private currentPage = 1;
  private nextShouldBeDisabled = false;
  private data: any = [];
  private isDesc = true;
  private _searchKey = '';

  private _dataArray: any[];
  private _dataArray$: Subject<any[]> = new Subject<any[]>();

  private _iterableDataArray: any[] = [];
  private _iterableDataArray$: Subject<any[]> = new Subject<any[]>();

  public paginationInfo: {
    firstItem: number;
    lastItem: number;
    allItems: number;
    activePage: number;
  } = {
    firstItem: 1,
    lastItem: this.itemsPerPage,
    allItems: this.dataArrayUnmodified.length,
    activePage: this.currentPage,
  };

  public set dataArray(data: any[]) {
    this._dataArray = data;
    this._dataArray$.next(this.dataArray);
  }

  public get dataArray(): any[] {
    return this._dataArray;
  }

  public dataArrayChange(): Observable<any[]> {
    return this._dataArray$;
  }

  public set iterableDataArray(data: any[]) {
    this._iterableDataArray = data;
    this._iterableDataArray$.next(this.iterableDataArray);
  }

  public get iterableDataArray(): any[] {
    return this._iterableDataArray;
  }

  public iterableDataArrayChange(): Observable<any[]> {
    return this._iterableDataArray$;
  }

  constructor(private renderer: Renderer2, private el: ElementRef, private ngZone: NgZone) {
    this.dataArrayChange()
      .pipe()
      .subscribe((newData: any[]) => {
        this.dataArrayUnmodified = newData;

        setTimeout(() => {
          this.iterableDataArray = this.dataArrayUnmodified.slice(
            this.itemsPerPage * (this.currentPage - 1),
            this.itemsPerPage * (this.currentPage - 1) + this.itemsPerPage
          );
          this.data = this.iterableDataArray;
          this.updatePaginationInfo();
        }, 0);
      });

    this.iterableDataArrayChange()
      .pipe(distinctUntilChanged())
      .subscribe(() => {
        if (this.data && this._searchKey.length == 0 && this.currentPage == 1) {
          this.paginationInfo.firstItem = this.data.findIndex((el: any) => el === this.data[0]) + 1;
          this.paginationInfo.lastItem =
            this.data.findIndex((el: any) => el == this.data[this.data.length - 1]) + 1;
        }
        if (
          this.iterableDataArray &&
          this.iterableDataArray.findIndex((el: any) => el == this.highlightedTableRow) === -1
        ) {
          if (this.highlightedTableRow !== null) {
            this.highlightedTableRow = null;
            this.rowHighlight.emit(null);
          }
        }
      });

    const click$ = fromEvent(this.el.nativeElement, 'click');
    click$.subscribe((event: any) => {
      // Workaround for situation when fromEvent is not able to emit value from EventEmitter
      this.ngZone.run(() => {
        this.markHighlightAndEmit(event);
      });
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['itemsPerPage']) {
      this.iterableDataArray = this.dataArrayUnmodified.slice(
        this.itemsPerPage * (this.currentPage - 1),
        this.itemsPerPage * (this.currentPage - 1) + this.itemsPerPage
      );
      if (!changes['itemsPerPage'].isFirstChange()) {
        this.updatePaginationInfo();
      }
    }
  }

  public calculatePagesNumber(): number {
    return Math.ceil(this.dataArrayUnmodified.length / this.itemsPerPage);
  }

  public updatePaginationInfo(): void {
    if (this.dataArray) {
      const firstAndLastIndexes = {
        first:
          this.dataArray.findIndex((el: any) => el === this.iterableDataArray[0]) + 1 ||
          this.iterableDataArray.findIndex((el: any) => el === this.iterableDataArray[0]) + 1,
        last:
          this.dataArray.findIndex(
            (el: any) => el === this.iterableDataArray[this.iterableDataArray.length - 1]
          ) + 1 ||
          this.iterableDataArray.findIndex(
            (el: any) => el === this.iterableDataArray[this.iterableDataArray.length - 1]
          ) + 1,
      };

      this.paginationInfo = {
        firstItem: firstAndLastIndexes.first,
        lastItem: firstAndLastIndexes.last,
        allItems: this.dataArrayUnmodified.length,
        activePage: this.currentPage,
      };
    }
  }

  public nextPage(switchToLastPage?: boolean): void {
    if (this.currentPage < this.calculatePagesNumber()) {
      if (switchToLastPage) {
        this.currentPage = this.calculatePagesNumber();
      } else {
        this.currentPage++;
      }
      this.iterableDataArray = this.dataArrayUnmodified.slice(
        this.itemsPerPage * (this.currentPage - 1),
        this.itemsPerPage * (this.currentPage - 1) + this.itemsPerPage
      );
      this.updatePaginationInfo();

      const firstAndLastIndexes = {
        first: this.dataArrayUnmodified.findIndex((el: any) => el == this.iterableDataArray[0]) + 1,
        last:
          this.dataArrayUnmodified.findIndex(
            (el: any) => el == this.iterableDataArray[this.iterableDataArray.length - 1]
          ) + 1,
      };

      this.paginationInfo.firstItem = firstAndLastIndexes.first;
      this.paginationInfo.lastItem = firstAndLastIndexes.last;
    }
  }

  public prevPage(switchToFirstPage?: boolean): void {
    if (this.currentPage > 1) {
      if (switchToFirstPage) {
        this.currentPage = 1;
      } else {
        this.currentPage--;
      }
      this.iterableDataArray = this.dataArrayUnmodified.slice(
        this.itemsPerPage * (this.currentPage - 1),
        this.itemsPerPage * (this.currentPage - 1) + this.itemsPerPage
      );
      this.updatePaginationInfo();
    }

    const firstAndLastIndexes = {
      first: this.dataArrayUnmodified.findIndex((el: any) => el == this.iterableDataArray[0]) + 1,
      last:
        this.dataArrayUnmodified.findIndex(
          (el: any) => el == this.iterableDataArray[this.iterableDataArray.length - 1]
        ) + 1,
    };

    this.paginationInfo.firstItem = firstAndLastIndexes.first;
    this.paginationInfo.lastItem = firstAndLastIndexes.last;
  }

  public disablePrevious(): boolean {
    return this.currentPage === 1;
  }

  public disableNext(): boolean | undefined {
    if (this.dataArrayUnmodified) {
      return this.currentPage >= this.calculatePagesNumber() || this.nextShouldBeDisabled;
    }
  }

  private filterIterableArray(searchKey: string): any {
    if (this.dataArray) {
      const filter = this.dataArray.filter((obj: Array<any>) => {
        return Object.keys(obj).some((key: any) => {
          if (obj[key] !== null) {
            return obj[key]
              .toString()
              .toLowerCase()
              .includes(searchKey);
          }
        });
      });

      this.iterableDataArray = filter.slice(
        this.itemsPerPage * (this.currentPage - 1),
        this.itemsPerPage * (this.currentPage - 1) + this.itemsPerPage
      );
      this.nextShouldBeDisabled = this.iterableDataArray.length < this.itemsPerPage;
      this.updatePaginationInfo();
      return this.iterableDataArray;
    }
  }

  public performSearch(searchKey: string): any {
    this._searchKey = searchKey;

    if (!searchKey && this.dataArray) {
      this.nextShouldBeDisabled = false;
      this.data = this.dataArray.slice(
        this.itemsPerPage * (this.currentPage - 1),
        this.itemsPerPage * (this.currentPage - 1) + this.itemsPerPage
      );
      this._iterableDataArray$.next(this.data);
      return this.dataArray.slice(
        this.itemsPerPage * (this.currentPage - 1),
        this.itemsPerPage * (this.currentPage - 1) + this.itemsPerPage
      );
    } else {
      this.data = this.filterIterableArray(searchKey.toLowerCase());
      return this.filterIterableArray(searchKey.toLowerCase());
    }
  }

  public sortArray(property: string): any {
    this.dataArray = this.dataArrayUnmodified.slice();
    this.isDesc = !this.isDesc; //change the direction
    property = property.replace(/ /g, '_').toLowerCase();
    const direction = this.isDesc ? 1 : -1;
    this.dataArray.sort((a, b) => {
      if (a[property] < b[property]) {
        return -1 * direction;
      } else if (a[property] > b[property]) {
        return 1 * direction;
      } else {
        return 0;
      }
    });
  }

  private _getClosestEl(el: any, selector: string): any {
    for (; el && el !== document; el = el.parentNode) {
      if (el.matches && el.matches(selector)) {
        return el;
      }
    }
    return null;
  }

  private markHighlightAndEmit(event: any) {
    const tableRow = this._getClosestEl(event.target, 'tr');
    const tbody = this._getClosestEl(event.target, 'tbody');

    if (tbody && event.target) {
      tbody.childNodes.forEach((row: any, index: number) => {
        if (row === tableRow) {
          this.highlightedTableRow = this.iterableDataArray[index];
        }

        if (row !== tableRow) {
          if (row.classList && row.classList.contains('tr-color-selected')) {
            this.renderer.removeClass(row, 'tr-color-selected');
          }
        }
      });
      this.rowHighlight.emit(this.highlightedTableRow);
      this.renderer.addClass(tableRow, 'tr-color-selected');
    }
  }
}
